.section {
   &__process {
      padding: 267px 0 0;
      overflow: hidden !important;
      position: relative;
      @media (max-width: 992px) {
         padding: 150px 0;
      }
      &::after {
         content: url(..//img/rosemary.png);
         position: absolute;
         right: 0;
         top: 0;
         z-index: 0 !important;
         @media (max-width: 1200px) {
            top: -200px;
         }
         @media (max-width: 768px) {
            right: -100px;
            top: -300px;
         }
      }
      &::before {
         content: url(..//img/rosemary.png);
         position: absolute;
         left: 0;
         bottom: 0;
         z-index: 0 !important;
         transform: rotate(180deg);
         @media (max-width: 1200px) {
            bottom: 200px;
            left: -200px;
         }
         @media (max-width: 768px) {
            left: -100px;
            bottom: 100px;
         }
      }
   }
}
.process {
   &__title {
      text-align: center;
      margin-bottom: 174px !important;
      @media (max-width: 768px) {
         margin-bottom: 100px !important;
      }
      @media (max-width: 350px) {
         font-size: 36px;
      }
   }
   &__body {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      row-gap: 118px;
      column-gap: 274px;
      @media (max-width: 1440px) {
         column-gap: 150px;
      }
      @media (max-width: 1200px) {
         column-gap: 100px;
         row-gap: 50px;
      }
      @media (max-width: 992px) {
         row-gap: 100px;
      }
   }
   &__item {
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: 1;
      @media (max-width: 992px) {
         flex-direction: column !important;
      }
      @media (max-width: 450px) {
         gap: 20px !important;
      }
      &:first-child {
         gap: 102px;
         flex: 0 1 100%;
         padding-top: 0 !important;
         @media (max-width: 992px) {
            gap: 46px;
            .process__item-content {
               max-width: 100%;
            }
         }
         .process__item-content {
            max-width: 616px;
            width: 100%;
         }
      }
      &:nth-child(2) {
         align-items: flex-end;
         flex-direction: column;
         gap: 46px;
         flex: 0 1 30%;
         @media (max-width: 1440px) {
            flex: 0 1 40%;
         }
         @media (max-width: 992px) {
            flex: 0 1 100%;
         }
      }
      &:nth-child(3) {
         flex-direction: column-reverse;
         align-items: flex-start;
         gap: 46px;
         flex: 0 1 30%;
         padding-bottom: 800px;
         @media (max-width: 1440px) {
            flex: 0 1 40%;
         }
         @media (max-width: 992px) {
            flex: 0 1 100%;
            padding-bottom: 0;
         }
      }
   }
   &__item-img {
      width: 100%;
      img {
         width: 100%;
         height: auto;
      }
   }
   &__item-title {
      font-family: "Rufina";
      font-style: normal;
      font-weight: 700;
      font-size: 88px;
      line-height: 1.2;
      color: #000000;
      @media (max-width: 768px) {
         font-size: 64px;
      }
      @media (max-width: 450px) {
         font-size: 48px;
      }
   }
}
