.section {
   &__balance {
      padding: 0 0 256px;
      @media (max-width: 992px) {
         padding: 0 0 150px;
      }
   }
}
.balance {
   &__header {
      text-align: center;
      max-width: 1000px;
      width: 100%;
      margin: 0 auto 126px;
   }
   &__content {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 60px;
      @media (max-width: 1200px) {
         flex-direction: column;
         max-width: 600px;
         margin: 0 auto;
      }
   }
   &__item {
      width: 100%;
      height: 700px;
      position: relative;
      background: url(..//img/balance_img1.png) center no-repeat;
      background-size: cover !important;
      @media (max-width: 480px) {
         height: 500px;
      }
      &:nth-child(2) {
         background: url(..//img/balance_img2.png) center no-repeat;
      }
      &:nth-child(3) {
         background: url(..//img/balance_img3.png) center no-repeat;
      }
   }
   &__item-link {
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: absolute;
      top: 48px;
      left: 40px;
      right: 40px;
      color: #233000;
      transition: all 0.3s ease;
      &:hover {
         left: 60px;
         right: 60px;
         transition: all 0.3s ease;
      }
      @media (max-width: 480px) {
         font-size: 30px;
         top: 20px;
         left: 20px;
         right: 20px;
         &:hover {
            left: 30px;
            right: 30px;
         }
      }
   }
}
