footer {
   background: #233000;
   border: 2px solid #000000;
}
.footer {
   padding: 132px 0;
   display: flex;
   justify-content: space-between;
   @media (max-width: 1200px) {
      flex-wrap: wrap;
      gap: 30px;
      padding: 60px 0;
   }
   @media (max-width: 768px) {
      justify-content: center;
      flex-direction: column;
      align-items: center;
      text-align: center;
   }
   &__subtitle {
      color: #ffffff !important;
   }
   &__logo {
      color: #ffffff;
      max-width: 188px;
      width: 100%;
      @media (max-width: 1440px) {
         font-size: 48px;
      }
      @media (max-width: 450px) {
         font-size: 36px;
      }
   }
   &__title {
      margin-bottom: 55px;
      @media (max-width: 1440px) {
         font-size: 28px;
         margin-bottom: 30px;
      }
      @media (max-width: 450px) {
         font-size: 24px;
      }
   }
   &__text {
      margin-bottom: 28px;
      max-width: 224px;
      width: 100%;
   }
   &__form {
      display: flex;
      align-items: center;
      gap: 28px;
      height: 96px;
      margin-bottom: 15px;
      @media (max-width: 1440px) {
         gap: 10px;
         height: 70px;
      }
      @media (max-width: 768px) {
         width: 100%;
      }
      @media (max-width: 550px) {
         flex-direction: column;
         height: auto;
      }
   }
   &__form-input {
      border: 2px solid #ffffff;
      background: inherit;
      padding: 30px 40px;
      height: 100%;
      min-width: 523px;
      width: 100%;
      @media (max-width: 1440px) {
         min-width: 420px;
         padding: 20px 30px;
      }
      @media (max-width: 768px) {
         width: 100%;
         min-width: fit-content !important;
      }
      @media (max-width: 450px) {
         padding: 20px 10px;
         font-size: 20px;
      }
      &::placeholder {
         color: #ffffff;
      }
      &::-moz-placeholder {
         color: #ffffff;
      }
   }
   &__form-btn {
      background: #5e6600;
      border: none;
      padding: 20px 46px;
      margin-bottom: 0 !important;
      height: 100%;
      cursor: pointer;
      transition: all 0.3s ease;
      &:hover {
         background: #4e5500;
         transition: all 0.3s ease;
      }
      @media (max-width: 1440px) {
         padding: 20px 30px;
      }
      @media (max-width: 550px) {
         width: 100%;
         padding: 10px 0;
      }
   }
   &__subtext {
      font-family: "Lato";
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 1.21;
      letter-spacing: 0.02em;
      color: #ffffff;
   }
}
.copyright {
   padding: 58px 0;
   border-top: 2px dashed #ffffff;
   @media (max-width: 768px) {
      padding: 30px 0;
   }
}
.copy {
   display: flex;
   align-items: center;
   justify-content: space-between;
   @media (max-width: 768px) {
      flex-direction: column;
      justify-content: center;
      gap: 15px;
      text-align: center;
   }
   &__icons {
      display: flex;
      align-items: center;
      gap: 16px;
      img {
         transition: all 0.3s ease;
         &:hover {
            opacity: 0.7;
            transition: all 0.3s ease;
         }
      }
   }
}
