.section {
   &__reservation {
      padding: 0 0 150px;
      &-main {
         padding: 0 0 382px;
         @media (max-width: 992px) {
            padding: 0 0 150px;
         }
      }
      &-contact {
         padding: 0 0 150px;
         .reservation__header {
            position: relative;
            max-width: 985px;
            width: 100%;
            margin: 0 auto;
            &::after {
               content: url(..//img/mint.png);
               position: absolute;
               left: -309px;
               top: -164px;
               @media (max-width: 1440px) {
                  left: -150px;
               }
               @media (max-width: 1200px) {
                  left: 50%;
                  transform: translate(-50%, 0);
               }
               @media (max-width: 768px) {
                  top: -130px;
               }
            }
         }
         .reservation__form {
            &-input {
               flex: 0 1 100%;
               &-text {
                  background: url(..//img/arrow-down.svg) right no-repeat;
                  background-position: 97.5%;
                  padding: 30px 90px 30px 40px;
                  width: 100%;
                  @media (max-width: 1440px) {
                     padding: 20px 70px 20px 30px !important;
                  }
                  @media (max-width: 768px) {
                     background-position: 95%;
                  }
                  @media (max-width: 480px) {
                     background-position: 93%;
                  }
                  @media (max-width: 400px) {
                     padding: 20px 50px 20px 20px !important;
                  }
               }
               &.small {
                  flex: 0 1 48.5%;
                  @media (max-width: 1662px) {
                     flex: 0 1 48%;
                  }
                  @media (max-width: 1440px) {
                     flex: 0 1 47.8%;
                  }
                  @media (max-width: 1200px) {
                     flex: 0 1 48.9%;
                  }
                  @media (max-width: 992px) {
                     flex: 0 1 48.5%;
                  }
                  @media (max-width: 768px) {
                     flex: 0 1 100%;
                  }
               }
            }
         }
      }
   }
}
.reservation {
   text-align: center;
   &__form {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      gap: 48px;
      margin-top: 133px;
      @media (max-width: 1200px) {
         gap: 20px;
      }
      @media (max-width: 768px){
         margin-top: 50px;
      }
      @media (max-width: 480px) {
         margin-top: 50px;
      }
   }
   input[type="date"]::-webkit-calendar-picker-indicator {
      background: url(..//img/arrow-down.svg) center no-repeat;
   }
   input[type="time"]::-webkit-datetime-edit-ampm-field {
      text-transform: lowercase;
   }
   input[type="time"]::-webkit-calendar-picker-indicator {
      background: url(..//img/arrow-down.svg) center no-repeat;
      cursor: pointer;
   }
   &__form-input {
      flex: 0 1 30%;
      border: 2px solid #000000;
      padding: 30px 40px;
      background: #fff;
      height: 80px;
      width: 100%;
      &-text {
         background: url(..//img/arrow-down.svg) right no-repeat;
         background-position: 90%;
         padding: 30px 90px 30px 40px;
         width: 100%;
         @media (max-width: 1440px) {
            padding: 20px 70px 20px 30px !important;
         }
         @media (max-width: 768px) {
            background-position: 95%;
         }
         @media (max-width: 480px) {
            background-position: 93%;
         }
         @media (max-width: 400px) {
            padding: 20px 50px 20px 20px !important;
         }
      }
      &::placeholder {
         color: #000000;
      }
      &::-moz-placeholder {
         color: #000000;
      }
      @media (max-width: 1440px) {
         padding: 20px 30px;
      }
      @media (max-width: 1200px) {
         flex: 0 1 47%;
      }
      @media (max-width: 768px) {
         flex: 0 1 100%;
      }
      @media (max-width: 400px) {
         padding: 20px;
         width: 100%;
      }
   }
   &__form-btn {
      flex: 0 1 30%;
      height: 80px;
      background: #233000;
      border: 2px solid #000000;
      transition: all 0.3s ease;
      &:hover {
         background: #324500;
         transition: all 0.3s ease;
      }
      cursor: pointer;
      @media (max-width: 1200px) {
         flex: 0 1 47%;
      }
      @media (max-width: 550px) {
         flex: 0 1 100%;
      }
   }
}
