.section {
   &__fashion {
      padding: 0 0 342px;
      @media (max-width: 992px) {
         padding: 0 0 150px;
      }
      &-blog {
         padding: 128px 0 226px;
         @media (max-width: 992px) {
            padding: 150px 0;
         }
         .fashion {
            flex-wrap: wrap;
            &__item {
               &:nth-child(3) {
                  .fashion__item-img {
                     background: url(..//img/fashion_img3.png) center no-repeat;
                  }
               }
               &:nth-child(4) {
                  .fashion__item-img {
                     background: url(..//img/fashion_img4.png) center no-repeat;
                  }
               }
               &:nth-child(5) {
                  .fashion__item-img {
                     background: url(..//img/fashion_img5.png) center no-repeat;
                  }
               }
               &:nth-child(6) {
                  .fashion__item-img {
                     background: url(..//img/fashion_img6.png) center no-repeat;
                  }
               }
            }
            &__pagination {
               display: flex;
               justify-content: center;
               align-items: center;
               gap: 40px;
               list-style: none;
               margin-top: 150px;
               @media (max-width: 768px){
                  margin-top: 100px;
               }
               @media (max-width: 550px) {
                  gap: 20px;
               }
               @media (max-width: 480px) {
                  flex-wrap: wrap;
               }
               &-btn {
                  padding: 10px 20px;
                  border: 2px solid #000000;
                  transition: all 0.3s ease;
                  &:hover {
                     background: #000000;
                     transition: all 0.3s ease;
                     .text {
                        color: #ffffff !important;
                        transition: all 0.3s ease;
                     }
                  }
                  .text {
                     color: #000000 !important;
                     transition: all 0.3s ease;
                  }
               }
            }
         }
      }
   }
}
.fashion {
   display: flex;
   justify-content: center;
   align-items: center;
   gap: 60px;
   @media (max-width: 992px) {
      flex-wrap: wrap;
   }
   &__item {
      flex: 0 1 46%;
      @media (max-width: 992px) {
         flex: 0 1 100%;
      }
      &:nth-child(2) {
         .fashion__item-img {
            background: url(..//img/fashion_img2.png) center no-repeat;
         }
      }
   }
   &__item-img {
      width: 100%;
      position: relative;
      background: url(..//img/fashion_img1.png) center no-repeat;
      background-size: cover;
      height: 792px;
      @media (max-width: 1440px) {
         height: 600px;
      }
      @media (max-width: 480px) {
         height: 400px;
      }
   }
   &__item-label {
      position: absolute;
      right: 12px;
      top: 12px;
      font-family: "Rufina";
      font-style: normal;
      font-weight: 700;
      font-size: 26px;
      line-height: 2;
      color: #ffffff;
      padding: 7px 53px;
      background: #5e6600;
      box-shadow: 1px 1px 12px rgba(0, 0, 0, 0.08);
      @media (max-width: 480px) {
         font-size: 24px;
         padding: 10px 30px;
      }
   }
   &__item-author {
      display: flex;
      align-items: center;
      gap: 32px;
      @media (max-width: 1200px) {
         gap: 14px;
      }
      @media (max-width: 480px) {
         flex-direction: column;
         gap: 4px;
         margin-bottom: 20px;
      }
   }
   li:first-child::marker {
      color: #ffffff;
   }
   li::marker {
      color: #5e6600;
      @media (max-width: 1200px) {
         color: #ffffff;
      }
   }
   &__item-subtext {
      color: #5e6600 !important;
   }
   &__item-title {
      padding-bottom: 18px;
      border-bottom: 4px dashed #000000;
      margin-bottom: 18px;
      @media (max-width: 1200px) {
         font-size: 36px;
      }
      @media (max-width: 350px) {
         font-size: 30px;
      }
   }
   &__item-text {
      font-family: "Lato";
      font-style: normal;
      font-weight: 700;
      font-size: 22px;
      line-height: 1.4;
      letter-spacing: 0.02em;
      color: #4d4d4d;
      margin-bottom: 46px;
      @media (max-width: 480px) {
         font-size: 20px;
      }
   }
   &__item-link {
      display: flex;
      align-items: center;
      gap: 10px;
      color: #000000 !important;
      transition: all 0.3s ease;
      &:hover {
         gap: 20px;
         transition: all 0.3s ease;
      }
      picture {
         display: flex;
         align-items: center;
      }
   }
}
