.section {
   &__info {
      padding: 128px 0 236px;
      @media (max-width: 992px) {
         padding: 128px 0 150px;
      }
   }
}
.info {
   display: flex;
   justify-content: space-between;
   gap: 60px;
   width: 100%;
   @media (max-width: 1200px) {
      flex-direction: column;
      justify-content: center;
      gap: 150px;
   }
   &__body {
      display: flex;
      flex-direction: column;
      gap: 130px;
      width: 100%;
      @media (max-width: 1200px) {
         max-width: 100%;
         gap: 40px;
      }
   }
   &__title {
      font-family: "Lato";
      font-style: italic;
      font-weight: 700;
      font-size: 38px;
      line-height: 140%;
      color: #5e6600;
      margin-left: 98px;
      @media (max-width: 992px) {
         margin-left: 20px;
         font-size: 32px;
      }
      @media (max-width: 768px) {
         font-size: 26px;
      }
      @media (max-width: 480px) {
         font-size: 24px;
      }
      @media (max-width: 400px) {
         margin-left: 0;
      }
   }
   &__tegs {
      display: flex;
      align-items: center;
      gap: 18px;
      @media (max-width: 992px) {
         flex-wrap: wrap;
      }
      @media (max-width: 480px) {
         gap: 10px;
      }
      .subtitle-big {
         color: #000000 !important;
      }
   }
   &__label {
      color: #000000 !important;
      letter-spacing: 0.02em;
      padding: 10px 20px;
      border: 1px solid #000000;
      @media (max-width: 480px) {
         padding: 5px 10px;
      }
   }
   &__contact {
      display: flex;
      align-items: center;
      gap: 60px;
      @media (max-width: 992px) {
         gap: 30px;
      }
      @media (max-width: 550px) {
         flex-direction: column;
         justify-content: center;
         text-align: center;
      }
   }
   &__contact-content {
      max-width: 795px;
      width: 100%;
   }
   &__subtitle {
      color: #000000 !important;
      margin-bottom: 28px;
      @media (max-width: 992px) {
         margin-bottom: 10px;
      }
   }
   &__form {
      display: flex;
      align-items: center;
      border-bottom: 2px solid #000000;
      width: 100%;
      gap: 10px;
      padding-bottom: 5px;
      &-input {
         color: #000000 !important;
         width: 100%;
         border: none;
         &::placeholder {
            color: #000000;
         }
         &::-moz-placeholder {
            color: #000000;
         }
      }
      &-btn {
         border: none;
         background-color: #fff;
         cursor: pointer;
      }
   }
   &__aside {
      display: flex;
      flex-direction: column;
      gap: 176px;
      max-width: 366px;
      width: 100%;
      @media (max-width: 1200px) {
         gap: 100px;
         max-width: 100%;
      }
      @media (max-width: 992px) {
         gap: 50px;
      }
      &-title {
         color: #000000 !important;
         border-bottom: 4px dashed #000000;
         padding-bottom: 8px;
         margin-bottom: 38px;
      }
      &-subtitle {
         color: #000000 !important;
      }
      &-block {
         display: flex;
         align-items: center;
         justify-content: space-between;
         margin-bottom: 28px;
         &:last-child {
            margin-bottom: 0;
         }
      }
      &-text {
         font-family: "Lato";
         font-style: normal;
         font-weight: 400;
         font-size: 14px;
         line-height: 17px;
         letter-spacing: 0.02em;
         color: #000000;
         margin-bottom: 22px;
         &:last-child {
            margin-bottom: 0;
         }
         span {
            color: #b0b0b0;
         }
      }
      &-post {
         display: flex;
         align-items: center;
         gap: 26px;
         margin-bottom: 32px;
         &:last-child {
            margin-bottom: 0;
         }
      }
      &-label {
         font-family: "Rufina";
         font-style: normal;
         font-weight: 400;
         font-size: 20px;
         line-height: 126%;
         color: #000000;
         margin-bottom: 8px;
      }
      &-time {
         display: flex;
         align-items: center;
         gap: 24px;
         @media (max-width: 400px) {
            flex-direction: column;
            align-items: flex-start;
            list-style: none;
            gap: 5px;
         }
         li:first-child::marker {
            color: #fff !important;
         }
         .info__aside-text {
            color: #b0b0b0;
            margin-bottom: 0;
         }
      }
      &-tags {
         display: flex;
         align-items: center;
         flex-wrap: wrap;
         gap: 12px;
      }
   }
   &__swiper {
      position: relative;
      max-width: 100%;
      width: 100%;
      max-height: 848px;
      height: 100%;
      display: flex;
      gap: 60px;
      @media (max-width: 1440px) {
         max-height: 500px;
      }
      @media (max-width: 1200px) {
         flex-direction: column;
         max-height: none;
         gap: 30px;
         img {
            object-fit: cover;
         }
      }
   }
   .swiper {
      margin: 0;
   }
   .mySwiper2 {
      max-width: 792px;
      width: 100%;
      @media (max-width: 1440px) {
         max-width: 500px;
      }
      @media (max-width: 1200px) {
         max-width: 100%;
         height: 600px;
      }
      @media (max-width: 992px) {
         height: auto;
      }
   }
   .swiper-slide {
      width: 100%;
      height: 100%;
      object-fit: cover;
      img {
         width: 100%;
         height: 100%;
      }
   }
   .mySwiper {
      @media (max-width: 1200px) {
         height: 200px;
      }
      @media (max-width: 450px) {
         height: 100px;
      }
   }
}
