.section {
   &__soon {
      padding: 138px 0 228px;
      min-height: 100vh;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      @media (max-width: 768px) {
         padding: 120px 0 150px;
      }
   }
}
.soon {
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
   height: 100%;
   &__title {
      z-index: 1;
      font-family: "Rufina";
      font-style: normal;
      font-weight: 700;
      font-size: 88px;
      line-height: 120%;
      letter-spacing: 14px;
      text-transform: uppercase;
      color: #ffffff;
      text-align: center;
      @media (max-width: 1200px) {
         font-size: 64px;
      }
      @media (max-width: 768px) {
         font-size: 48px;
      }
      @media (max-width: 480px) {
         font-size: 36px;
      }
      &-under {
         border-bottom: 2px dashed #ffffff;
         padding-bottom: 19px;
         max-width: 1000px;
         width: 100%;
         margin: 0 auto;
         margin-bottom: 19px;
      }
   }
   &__time {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 134px;
      gap: 136px;
      z-index: 1;
      @media (max-width: 992px) {
         gap: 50px;
         flex-wrap: wrap;
      }
      @media (max-width: 768px){
         margin-bottom: 50px;
      }
      @media (max-width: 480px) {
         gap: 20px;
      }
   }
   &__item {
      text-align: center;
   }
   &__text {
      color: #ffffff !important;
   }
   &__btn {
      padding: 20px 46px;
      border: 2px solid #ffffff;
      z-index: 1;
      transition: all 0.3s ease;
      @media (max-width: 400px) {
         width: 100%;
         padding: 20px 0;
         text-align: center;
      }
      &:hover {
         transition: all 0.3s ease;
         background-color: #ffffff;
         color: #233000;
      }
   }
}
