.section {
   &__feature {
      padding: 207px 0 320px;
      @media (max-width: 992px) {
         padding: 100px 0 150px;
      }
   }
}
.feature {
   display: flex;
   justify-content: center;
   align-items: center;
   gap: 60px;
   @media (max-width: 1200px) {
      flex-wrap: wrap;
   }
   &__item {
      text-align: center;
      @media (max-width: 1200px) {
         flex: 0 1 46%;
      }
      @media (max-width: 768px) {
         flex: 0 1 100%;
      }
   }
   &__item-img {
      background: #ebf0e4;
      height: 260px;
      width: 260px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 auto 39px;
      @media (max-width: 480px){
         transform: scale(.8);
         margin: 0 auto;
      }
      @media (max-width: 350px){
         transform: scale(.6);
         margin: -40px auto 0;
      }
   }
   &__item-title {
      margin-bottom: 39px;
      @media (max-width: 1440px) {
         margin-bottom: 20px;
         font-size: 36px;
      }
   }
}
