header {
   background: #233000;
   &.header {
      background: url(..//img/start_menu_bg.png) center no-repeat;
      background-size: cover !important;
      width: 100%;
   }
   &.header__about {
      background: url(..//img/start_about_bg.png) center no-repeat;
      background-size: cover !important;
      width: 100%;
   }
   &.header__soon {
      background: url(..//img/soon_bg.png) center no-repeat;
      background-size: cover !important;
      width: 100%;
      position: relative;
      &::after {
         content: "";
         position: absolute;
         left: 0;
         top: 0;
         right: 0;
         bottom: 0;
         width: 100%;
         height: 100%;
         background: #233000;
         opacity: 0.72;
         z-index: 0 !important;
      }
   }
   &.header__contact {
      background: url(..//img/start_contact_bg.png) center no-repeat;
      background-size: cover !important;
      width: 100%;
   }
   &.header__blog {
      background: url(..//img/start_blog_bg.png) center no-repeat;
      background-size: cover !important;
      width: 100%;
   }
   &.header__blogpost {
      background: url(..//img/start_blogpost_bg.png) center no-repeat;
      background-size: cover !important;
      width: 100%;
   }
   .section {
      &__start {
         padding: 102px 0 222px;
         position: relative;
         @media (max-width: 992px) {
            padding: 60px 0 100px;
         }
         .container {
            position: relative;
         }
         &-menu {
            @media (max-width: 992px) {
               .start__content {
                  text-align: center;
                  .start__title,
                  .start__text {
                     margin: 0 auto;
                  }
               }
            }
            .start__title {
               max-width: 779px;
            }
         }
         &-about {
            padding: 300px 0 400px;
            height: 100%;
            @media (max-width: 992px) {
               padding: 150px 0;
               .start__content {
                  text-align: center;
                  position: relative !important;
                  margin: 0 auto;
                  .start__title,
                  .start__text {
                     margin: 0 auto;
                  }
               }
            }
            .start__item {
               position: relative;
               .start__content {
                  position: absolute;
                  right: 0;
               }
            }
            .start__title {
               max-width: 879px;
            }
         }
         &-contact {
            padding: 200px 0 130px;
            .start {
               display: flex;
               justify-content: center;
               align-items: flex-start;
               padding: 0;
               @media (max-width: 1200px) {
                  flex-direction: column;
                  align-items: center;
                  text-align: center;
               }
               &__item {
                  &.second-item {
                     flex-direction: column;
                     max-width: 696px;
                     width: 100%;
                     margin-top: 350px !important;
                     @media (max-width: 1662px) {
                        max-width: 550px;
                     }
                     @media (max-width: 1440px) {
                        max-width: 500px;
                     }
                     @media (max-width: 1200px) {
                        margin-top: 100px !important;
                        max-width: 550px;
                        gap: 0;
                     }
                     .start__item-header {
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        padding-bottom: 38px;
                        border-bottom: 2px dashed #ffffff;
                        margin-bottom: 38px;
                        width: 100%;
                        @media (max-width: 1200px) {
                           padding-bottom: 15px;
                           margin-bottom: 15px;
                        }
                        @media (max-width: 400px) {
                           flex-direction: column;
                           border-bottom: none;
                        }
                     }
                     .start__item-text {
                        color: #ffffff !important;
                     }
                     .start__item-body {
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        width: 100%;
                        text-align: left;
                        @media (max-width: 450px) {
                           flex-wrap: wrap;
                           gap: 25px;
                           justify-content: center;
                           text-align: center;
                        }
                        @media (max-width: 400px) {
                           flex-direction: column;
                        }
                     }
                  }
               }
            }
         }
         &-blog {
            .start__content {
               text-align: center;
               margin: 0 auto;
            }
         }
         &-blogpost {
            .start__item {
               flex-direction: column;
               width: 100%;
               align-items: flex-start;
               @media (max-width: 768px) {
                  align-items: center;
               }
               .start__title {
                  max-width: 100%;
                  width: 100%;
                  margin-top: 46px;
                  @media (max-width: 768px) {
                     text-align: center;
                  }
               }
               &-author {
                  margin-top: 32px;
                  display: flex;
                  align-items: center;
                  gap: 32px;
                  @media (max-width: 768px) {
                     gap: 14px;
                     list-style: none;
                  }
                  @media (max-width: 480px) {
                     flex-direction: column;
                     gap: 4px;
                     margin-bottom: 20px;
                  }
                  li:first-child::marker {
                     color: none !important;
                  }
                  li::marker {
                     color: #ffffff;
                  }
               }
            }
            .start__label {
               font-family: "Rufina";
               font-style: normal;
               font-weight: 700;
               font-size: 26px;
               line-height: 48px;
               color: #ffffff;
               padding: 7px 53px;
               border: 2px solid #ffffff;
               filter: drop-shadow(1px 1px 12px rgba(0, 0, 0, 0.08));
            }
         }
      }
   }
   .start {
      &__item {
         display: flex;
         justify-content: space-between;
         align-items: center;
         @media (max-width: 992px) {
            flex-direction: column;
         }
         &.second-item {
            margin-top: 800px;
            @media (max-width: 1440px) {
               margin-top: 500px;
               .start__content {
                  max-width: 500px;
                  img {
                     width: 100%;
                     height: auto;
                     object-fit: cover;
                  }
               }
            }
            @media (max-width: 1200px) {
               flex-direction: column;
               gap: 50px;
               .start__content {
                  max-width: 700px;
                  gap: 20px;
                  width: 100%;
               }
            }
            @media (max-width: 992px) {
               margin-top: 100px;
            }
         }
      }
      &__subtitle {
         color: #ffffff;
         max-width: 493px;
         width: 100%;
         @media (max-width: 768px) {
            font-size: 48px;
            max-width: 565px;
         }
         @media (max-width: 400px) {
            font-size: 36px;
         }
      }
      &__content {
         display: flex;
         flex-direction: column;
         gap: 50px;
         position: relative;
         &.reverse {
            flex-direction: column-reverse;
            @media (max-width: 1200px) {
               flex-direction: column;
            }
         }
         @media (max-width: 768px) {
            gap: 20px;
            align-items: center;
         }
      }
      &__title {
         font-family: "Rufina";
         font-style: normal;
         font-weight: 700;
         font-size: 148px;
         line-height: 1.02;
         color: #ffffff;
         z-index: 1;
         max-width: 1057px;
         width: 100%;
         @media (max-width: 1440px) {
            max-width: 750px;
            font-size: 100px;
         }
         @media (max-width: 1200px) {
            font-size: 80px;
         }
         @media (max-width: 768px) {
            font-size: 64px;
         }
         @media (max-width: 480px) {
            font-size: 48px;
         }
         @media (max-width: 400px) {
            font-size: 36px;
         }
      }
      &__text {
         color: #ffffff;
         max-width: 565px;
         width: 100%;
      }
      &__scroll {
         color: #ffffff;
         transform: rotate(90deg);
         position: absolute;
         bottom: -150px;
         left: 0;
         cursor: pointer;
         &:hover::after {
            left: 80%;
            transition: all 0.3s ease;
            @media (max-width: 992px) {
               left: 250%;
            }
         }
         @media (max-width: 992px) {
            bottom: 0;
            position: relative;
            width: 50px;
         }
         @media (max-width: 768px) {
            margin-bottom: 140px;
         }
         @media (max-width: 480px) {
            font-size: 30px;
         }
         &::after {
            content: "";
            position: absolute;
            left: 110%;
            top: 50%;
            border: 2px dashed #ffffff;
            width: 300px;
            transition: all 0.3s ease;
            @media (max-width: 1440px) {
               width: 200px;
            }
            @media (max-width: 480px) {
               width: 100px;
            }
         }
      }
      &__img {
         position: relative;
         .first-img {
            position: absolute;
            top: 0px;
            right: 0;
            z-index: 0 !important;
            @media (max-width: 1440px) {
               width: 500px;
            }
            @media (max-width: 992px) {
               position: relative;
            }
            @media (max-width: 480px) {
               width: 100%;
            }
         }
      }
      &__img-block {
         display: flex;
         align-items: center;
         gap: 44px;
         z-index: 1;
         position: absolute;
         bottom: -900px;
         right: 150px;
         @media (max-width: 1440px) {
            bottom: -550px;
         }
         @media (max-width: 1200px) {
            right: 50px;
         }
         @media (max-width: 992px) {
            bottom: 20px;
            right: 0;
         }
         @media (max-width: 768px) {
            flex: 0 1 33%;
            gap: 10px;
         }
         img {
            width: 250px;
            z-index: 1 !important;
            animation: img-spin 30s 0s infinite linear;
            @media (max-width: 1440px) {
               width: 200px;
            }
            @media (max-width: 768px) {
               width: 150px;
            }
            @media (max-width: 480px){
               width: 100px;
            }
            @media (max-width: 350px){
               width: 70px;
            }
         }
      }
   }
}
.fixed__nav {
   position: fixed;
   left: 0;
   right: 0;
   top: 0;
   background: #233000;
   z-index: 999;
   transition: all 0.5s ease;
   .nav__body {
      padding: 15px 0;
   }
   @media (max-width: 1662px) and (max-height: 900px) {
      .nav__logo {
         width: 200px;
      }
      .nav__btn {
         padding: 10px 30px;
      }
   }
   @media (max-width: 480px) {
      .nav__logo {
         width: 130px;
      }
   }
}
.nav {
   &__body {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 46px 0;
      @media (max-width: 1662px) and (max-height: 900px) {
         padding: 25px 0;
      }
      @media (max-width: 992px) {
         padding: 15px 0;
      }
      @media (max-width: 550px) {
         flex-direction: column;
         justify-content: center;
      }
   }
   &__logo {
      z-index: 1 !important;
      svg {
         width: 100%;
         height: auto;
      }
      @media (max-width: 1200px) {
         width: 200px;
      }
      @media (max-width: 480px) {
         width: 130px;
      }
   }
   &__menu {
      position: fixed;
      background: url(..//img/burger_bg.png) center no-repeat;
      background-size: cover !important;
      left: 0;
      top: 0;
      min-height: 100vh;
      height: 100%;
      width: 100vw;
      display: none;
      &::after {
         content: "";
         position: fixed;
         top: 0;
         left: 0;
         right: 0;
         bottom: 0;
         width: 100%;
         height: 100%;
         background: #233000;
         opacity: 0.8;
         z-index: 0 !important;
      }
      &.active {
         display: flex;
         left: 0;
         top: 0;
         min-height: 100vh;
         height: 100%;
         width: 100vw;
         pointer-events: all;
         justify-content: space-around;
         align-items: center;
         z-index: 22;
         animation: fade 0.3s ease;
         @media (max-width: 992px) and (max-height: 800px) {
            overflow-y: scroll !important;
            align-items: flex-start;
            padding: 140px 0 30px;
         }
         @media (max-width: 550px) {
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: 30px;
         }
      }
   }
   &__list {
      z-index: 1;
      display: flex;
      flex-direction: column;
      gap: 28px;
      @media (max-width: 992px) {
         gap: 15px;
      }
      @media (max-width: 550px) {
         list-style: none;
         text-align: center;
      }
      li {
         transition: all 0.3s ease;
         &::marker {
            color: #ffffff;
            font-size: 200%;
            transition: all 0.3s ease;
         }
         &.hide{
            display: none;
            @media (max-width: 992px){
               display: block;
            }
         }
         &:hover {
            transition: all 0.3s ease;
            &::marker {
               color: #9caa00;
               transition: all 0.3s ease;
            }
            .nav__list-link {
               color: #9caa00;
               transition: all 0.3s ease;
            }
         }
      }
      &-link {
         font-family: "Rufina";
         font-style: normal;
         font-weight: 700;
         font-size: 66px;
         line-height: 1.24;
         text-transform: uppercase;
         color: #ffffff;
         transition: all 0.3s ease;
         @media (max-width: 1200px) {
            font-size: 48px;
         }
         @media (max-width: 992px) {
            font-size: 40px;
         }
         @media (max-width: 550px) {
            font-size: 32px;
         }
      }
   }
   &__info {
      z-index: 1;
      @media (max-width: 550px) {
         text-align: center;
      }
   }
   &__item {
      display: flex;
      align-items: center;
      gap: 45px;
      @media (max-width: 992px) {
         justify-content: space-between;
         gap: 0;
         width: 100%;
      }
   }
   &__info-break {
      border: 4px dashed #ffffff;
      width: 100%;
      margin: 8px 0 38px;
      @media (max-width: 550px) {
         margin: 8px 0 20px;
      }
   }
   &__info-text {
      max-width: 197px;
      width: 100%;
      margin-bottom: 38px;
      @media (max-width: 550px) {
         margin: 0 0 20px;
      }
      &.hide{
         display: none;
         @media (max-width: 992px){
            display: block;
         }
      }
   }
   &__info-icons {
      display: flex;
      align-items: center;
      gap: 18px;
      @media (max-width: 550px) {
         justify-content: center;
      }
      img {
         transition: all 0.3s ease;
         &:hover {
            opacity: 0.6;
            transition: all 0.3s ease;
         }
      }
   }
   &__contact {
      z-index: 1 !important;
      display: flex;
      align-items: center;
      gap: 26px;
      @media (max-width: 992px) {
         display: none;
      }
   }
   &__link {
      font-family: "Inter";
      font-style: normal;
      font-weight: 400;
      font-size: 26px;
      line-height: 1.19;
      color: #ffffff;
      transition: all 0.3s ease;
      &:hover {
         opacity: 0.7;
         transition: all 0.3s ease;
      }
   }
   &__btn {
      padding: 20px 48px;
      border: 2px solid #ffffff;
      transition: all 0.3s ease;
      @media (max-width: 1200px) {
         padding: 13px 30px;
         font-size: 28px;
         line-height: 1.5;
      }
      &:hover {
         background: #ffffff;
         color: #233000;
         transition: all 0.3s ease;
      }
   }
}
.burger {
   display: block;
   position: relative;
   height: 20px;
   width: 30px;
   z-index: 102;
   cursor: pointer;
   &.active-burger {
      position: absolute;
      top: 34px;
      left: 46px;
      &::before {
         background-color: #000000;
         transform: rotate(45deg);
         transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.3s all;
         top: 8px;
      }
      &::after {
         background-color: #000000;
         transform: rotate(-45deg);
         transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.3s all;
         bottom: 10px;
      }
      & span {
         transform: scale(0);
      }
   }
   &::before,
   &::after {
      content: "";
      position: absolute;
      height: 2px;
      width: 100%;
      background-color: #ffffff;
      transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.3s all;
   }
   &::before {
      top: 0;
   }
   &::after {
      bottom: 0;
   }
   span {
      position: absolute;
      top: 9px;
      right: 0;
      width: 100%;
      background-color: #ffffff;
      height: 2px;
      transform: scale(1);
      transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.3s all;
   }
}
