.section {
   &__menu {
      padding: 186px 0 218px;
      position: relative;
      overflow: hidden;
      @media (max-width: 992px) {
         padding: 100px 0 150px;
      }
      &::after {
         content: url(..//img/menu_bg.png);
         position: absolute;
         right: 0;
         top: -450px;
         transform: rotate(-115.48deg);
         z-index: -1 !important;
         @media (max-width: 1440px) {
            right: -300px;
         }
         @media (max-width: 992px) {
            right: -500px;
            top: -550px;
         }
         @media (max-width: 450px) {
            top: -600px;
         }
      }
      &-menu {
         &.section__menu::after {
            content: url(..//img/blueberry.png);
            top: -20px;
            transform: rotate(0deg);
            @media (max-width: 1662px) {
               top: -150px;
            }
            @media (max-width: 1440px) {
               right: -100px;
            }
            @media (max-width: 992px) {
               right: -200px;
               top: -250px;
            }
            @media (max-width: 450px) {
               top: -100px;
            }
         }
         .menu {
            display: flex;
            flex-direction: column;
            gap: 300px;
            @media (max-width: 1440px) {
               gap: 150px;
            }
            &__body {
               flex-direction: column;
               column-gap: 0;
               row-gap: 0;
               flex-wrap: nowrap;
            }
            &__header {
               margin: 0 auto 152px;
               text-align: center;
               @media (max-width: 1200px) {
                  margin: 0 auto 100px;
               }
               &.after {
                  position: relative;
                  &:after {
                     content: url(..//img/avocado.png);
                     width: 100%;
                     height: 100%;
                     position: absolute;
                     top: -50%;
                     left: 50%;
                     transform: translate(-50%, 50%);
                     @media (max-width: 1200px) {
                        height: 100px;
                     }
                     @media (max-width: 768px) {
                        display: none;
                     }
                  }
               }
            }
            &__content {
               display: flex;
               justify-content: center;
               align-items: center;
               gap: 102px;
               @media (max-width: 1440px) {
                  .menu__content-img {
                     width: 100%;
                     img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                     }
                  }
               }
               @media (max-width: 1200px) {
                  gap: 30px;
                  flex-direction: column;
                  max-width: 800px;
                  margin: 0 auto;
                  .menu__item {
                     max-width: 100%;
                  }
                  .menu__content-img {
                     height: 500px;
                  }
               }
               @media (max-width: 450px) {
                  .menu__content-img {
                     height: 400px;
                  }
               }
               &.reverse {
                  flex-direction: row-reverse;
                  @media (max-width: 1200px) {
                     flex-direction: column;
                  }
               }
               &-body {
                  display: flex;
                  flex-direction: column;
                  gap: 58px;
               }
            }
         }
      }
   }
}
.menu {
   &__header {
      max-width: 532px;
      width: 100%;
      margin-bottom: 152px;
      z-index: 1 !important;
   }
   &__body {
      display: flex;
      flex-wrap: wrap;
      column-gap: 60px;
      row-gap: 108px;
      justify-content: center;
      @media (max-width: 1440px) {
         column-gap: 40px;
         row-gap: 68px;
      }
      @media (max-width: 992px) {
         column-gap: 20px;
         row-gap: 48px;
      }
   }
   &__item {
      max-width: 792px;
      width: 100%;
      @media (max-width: 1662px) {
         max-width: 650px;
      }
      @media (max-width: 1440px) {
         max-width: 520px;
      }
      @media (max-width: 1200px) {
         max-width: 450px;
      }
      @media (max-width: 992px) {
         max-width: 350px;
      }
      @media (max-width: 768px) {
         max-width: 500px;
      }
   }
   &__item-price {
      text-align: right;
      padding-bottom: 12px;
      border-bottom: 4px dashed #000000;
      @media (max-width: 1200px) {
         font-size: 36px;
      }
      @media (max-width: 992px) {
         font-size: 32px;
      }
      @media (max-width: 400px) {
         font-size: 28px;
      }
   }
   &__item-title {
      color: #000000;
      margin: 24px 0;
      @media (max-width: 1440px) {
         font-size: 48px;
      }
      @media (max-width: 1200px) {
         font-size: 40px;
      }
      @media (max-width: 992px) {
         font-size: 36px;
      }
      @media (max-width: 400px) {
         font-size: 30px;
      }
   }
}
