.section {
   &__contact {
      padding: 278px 0 400px;
   }
}
.contact {
   display: flex;
   flex-direction: column;
   gap: 145px;
   @media (max-width: 480px) {
      gap: 60px;
   }
   &__item-text {
      color: #233000 !important;
      @media (max-width: 480px) {
         font-size: 24px;
      }
      a {
         color: #9caa00;
         &:hover {
            text-decoration: underline;
         }
      }
   }
   &__item {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 60px;
      @media (max-width: 992px) {
         flex-direction: column !important;
         text-align: center;
         gap: 30px !important;
      }
      &:first-child {
         gap: 202px;
         align-items: flex-end;
         @media (max-width: 1440px) {
            gap: 60px;
         }
         .contact__item-text {
            max-width: 507px;
            width: 100%;
            padding-bottom: 100px;
            @media (max-width: 992px) {
               padding-bottom: 0;
               margin: 0 auto;
            }
         }
         .contact__item-img {
            position: relative;
            &::after {
               content: url(..//img/orange.png);
               position: absolute;
               right: -283px;
               top: -120px;
               z-index: -1 !important;
               @media (max-width: 1440px) {
                  right: -150px;
               }
               @media (max-width: 768px) {
                  display: none;
               }
            }
         }
      }
      &.reverse {
         flex-direction: row-reverse;
         .contact__item-text {
            margin-bottom: 73px;
         }
      }
   }
   &__item-img {
      width: 100%;
      img {
         width: 100%;
         height: auto;
         z-index: 1 !important;
      }
   }
   &__item-btn {
      padding: 20px 46px;
      border: 2px solid #000000;
      transition: all 0.3s ease;
      &:hover {
         background: #000000;
         color: #fff !important;
         transition: all 0.3s ease;
      }
      @media (max-width: 350px) {
         width: 100%;
         padding: 20px 0;
         display: flex;
         justify-content: center;
      }
   }
}
