.section {
   &__delicious {
      background: url(..//img/delicious_bg.png) center no-repeat;
      background-size: cover !important;
      width: 100%;
      padding: 238px 0 188px;
      position: relative;
      @media (max-width: 550px) {
         padding: 150px 0;
      }
      @media (max-width: 400px) {
         padding: 100px 0;
      }
      &::after {
         content: "";
         position: absolute;
         left: 0;
         top: 0;
         right: 0;
         bottom: 0;
         width: 100%;
         height: 100%;
         background: #233000;
         opacity: 0.7;
         z-index: 0 !important;
      }
   }
}
.delicious {
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
   text-align: center;
   &__title {
      z-index: 1 !important;
      color: #fff !important;
   }
   &__text {
      z-index: 1 !important;
      color: #fff !important;
   }
   &__play {
      z-index: 1 !important;
      margin-top: 82px;
      @media (max-width: 400px) {
         margin-top: 40px;
         svg {
            width: 100px;
            height: 100px;
         }
      }
      path {
         transition: all 0.3s ease;
      }
      .delicious__bg {
         transition: all 0.3s ease;
      }
      &:hover {
         path {
            stroke-width: 21px;
            transition: all 0.3s ease;
         }
         .delicious__bg {
            opacity: 1;
            transition: all 0.3s ease;
         }
      }
   }
}
