* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
}
:focus-visible {
   outline: none;
}
body {
   overflow-x: hidden !important;
   .overlay {
      width: 100%;
      display: flex;
      flex-direction: column;
      min-height: 100vh;
      height: 100%;
   }
}
.main {
   flex: 1 1 auto;
}
header{
   overflow-x: hidden !important;
}
.container {
   max-width: $mw;
   margin: 0 auto;
   width: 100%;
   @media (max-width: $md1) {
      max-width: 1420px;
   }
   @media (max-width: $md2) {
      max-width: 1110px;
   }
   @media (max-width: $md3) {
      max-width: 970px;
   }
   @media (max-width: $md4) {
      max-width: 750px;
   }
   @media (max-width: $md5) {
      max-width: none;
      padding: 0 10px;
   }
}
a {
   text-decoration: none;
}
h1,
h2,
h3,
h4,
p,
span {
   margin: 0;
   padding: 0;
}
ul,
li {
   padding: 0;
   margin: 0;
}
@keyframes fade {
   0% {
      opacity: 0;
   }
   100% {
      opacity: 1;
   }
}
@keyframes img-spin {
   0% {
      transform: rotate(0deg);
   }
   100% {
      transform: rotate(360deg);
   }
}
/* Main */
.title {
   font-family: "Rufina";
   font-style: normal;
   font-weight: 700;
   font-size: 68px;
   line-height: 1.23;
   @media (max-width: 992px){
      font-size: 48px;
   }
   @media (max-width: 480px){
      font-size: 40px;
   }
}
.title-big {
   font-family: "Rufina";
   font-style: normal;
   font-weight: 700;
   font-size: 108px;
   line-height: 1.11;
   color: #000000;
   margin-bottom: 18px;
   @media (max-width: 1440px) {
      font-size: 80px;
   }
   @media (max-width: 768px) {
      font-size: 64px;
   }
   @media (max-width: 480px) {
      font-size: 48px;
   }
}
.subtitle {
   font-family: "Rufina";
   font-style: normal;
   font-weight: 700;
   font-size: 48px;
   line-height: 1.33;
   color: #000000;
   @media (max-width: 768px){
      font-size: 36px;
   }
}
.subtitle-big {
   font-family: "Rufina";
   font-style: normal;
   font-weight: 700;
   font-size: 32px;
   line-height: 1.41;
   color: #ffffff;
   @media (max-width: 768px){
      font-size: 28px;
   }
}
.text {
   font-family: "Lato";
   font-style: normal;
   font-weight: 400;
   font-size: 24px;
   line-height: 1.4;
   letter-spacing: 0.02em;
   color: #4d4d4d;
   @media (max-width: 480px) {
      font-size: 20px;
   }
}
.subtext {
   font-family: "Lato";
   font-style: normal;
   font-weight: 400;
   font-size: 18px;
   line-height: 1.38;
   letter-spacing: 0.02em;
   color: #ffffff;
}