.section {
   &__story {
      padding: 218px 0 300px;
      @media (max-width: 1440px){
         padding: 218px 0;
      }
      @media (max-width: 992px) {
         padding: 150px 0;
      }
   }
}
.story {
   &__header {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 107px;
      margin-bottom: 280px;
      @media (max-width: 1440px){
         margin-bottom: 150px;
      }
      @media (max-width: 992px) {
         flex-direction: column;
         gap: 50px;
         margin-bottom: 200px;
      }
      @media (max-width: 768px) {
         margin-bottom: 100px;
      }
   }
   &__header-content {
      max-width: 668px;
      width: 100%;
   }
   &__header-img {
      position: relative;
      img {
         width: 100%;
         height: auto;
         z-index: 1 !important;
      }
      &::after {
         content: url(..//img/story_bg.png);
         position: absolute;
         left: -300px;
         top: -124px;
         width: 100%;
         height: 100%;
         z-index: -1 !important;
         @media (max-width: 1440px) {
            left: -200px;
         }
         @media (max-width: 992px) {
            top: -50px;
            left: -100px;
         }
         @media (max-width: 768px) {
            display: none;
         }
      }
   }
   &__body {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(5, 1fr);
      grid-column-gap: 121px;
      grid-row-gap: 0px;
      width: 100%;
      @media (max-width: 992px) {
         display: flex;
         justify-content: center;
         align-items: center;
         flex-direction: column;
         gap: 50px;
      }
   }
   &__item {
      width: 100%;
      &-title {
         @media (max-width: 480px) {
            font-size: 36px;
         }
      }
      img {
         width: 100%;
         height: auto;
      }
      &:first-child {
         grid-area: 1 / 1 / 4 / 2;
         text-align: center;
         display: flex;
         flex-direction: column;
         gap: 24px;
      }
      &:nth-child(2) {
         grid-area: 1 / 2 / 2 / 3;
         .text {
            max-width: 510px;
         }
      }
      &:nth-child(3) {
         grid-area: 3 / 2 / 6 / 3;
         text-align: center;
         display: flex;
         flex-direction: column;
         gap: 24px;
      }
      &:nth-child(4) {
         grid-area: 5 / 1 / 6 / 2;
         text-align: right;
         @media (max-width: 480px) {
            text-align: left;
         }
         .text {
            max-width: 510px;
            float: right;
         }
      }
   }
}
