.cook {
   display: flex;
   justify-content: space-between;
   align-items: center;
   gap: 61px;
   @media (max-width: 1200px) {
      flex-direction: column-reverse;
      gap: 0;
   }
   &__item {
      &.first-item {
         img {
            width: 100%;
         }
      }
      &.second-item {
         max-width: 649px;
         width: 100%;
         @media (max-width: 1440px) {
            max-width: 400px;
         }
         @media (max-width: 1200px) {
            max-width: 600px;
            text-align: center;
            img {
               display: none;
            }
         }
         img {
            width: 70%;
            float: right;
            margin-top: 30px;
         }
      }
   }
}
