.section {
   &__review {
      padding: 0 0 145px;
   }
}
.review {
   position: relative;
   &__swiper {
      .swiper {
         &__btns {
            position: absolute;
            right: 10px;
            bottom: 0px;
            display: flex;
            align-items: center;
            gap: 41px;
            @media (max-width: 768px) {
               right: 50%;
               transform: translate(50%, 0);
               z-index: 99;
            }
         }
      }
      .slide {
         &__text {
            max-width: 1262px;
            width: 100%;
            margin-bottom: 86px;
            @media (max-width: 1200px) {
               font-size: 36px;
               margin-bottom: 40px;
            }
            @media (max-width: 550px) {
               font-size: 30px;
            }
            @media (max-width: 450px) {
               font-size: 24px;
            }
         }
         &__contact {
            display: flex;
            align-items: center;
            gap: 60px;
            @media (max-width: 1200px) {
               gap: 30px;
            }
            @media (max-width: 768px) {
               img {
                  width: 100px;
               }
            }
            margin-bottom: 60px;
         }
         &__title {
            color: #000000;
            margin-bottom: 6px;
            @media (max-width: 768px) {
               font-size: 28px;
            }
         }
         &__subtitle {
            color: #4d4d4d;
         }
      }
      .swiper-prev {
         transform: rotate(180deg);
         margin-right: 40px;
         transition: all 0.3s ease;
         &:hover {
            transform: rotate(180deg) translate(8px, 0);
            transition: all 0.3s ease;
         }
      }
      .swiper-next {
         transition: all 0.3s ease;
         &:hover {
            transform: translate(8px, 0);
            transition: all 0.3s ease;
         }
      }
      .swiper__btn {
         z-index: 99 !important;
         cursor: pointer;
      }
      .swiper__btn picture {
         display: flex;
         justify-content: center;
         align-items: center;
      }
      .swiper-pagination {
         bottom: -10px !important;
         z-index: 0 !important;
      }
   }
}
